<template>
	<view class="index">
		<view class="patch">
			<image :src="detail.links.patch.small" mode="heightFix"></image>
		</view>
		<view class="cell-group">
			<view class="cell-title">
				
			</view>
			<view class="cell-item">
				
			</view>
		</view>
	</view>
</template>

<script>
	// import PastLaunches from'../../common/common.js';
	export default {
		data() {
			return {
				LaunchesLatestData: [],
				detail: ''
			}
		},
		//onLoad 生命周期函数前，加async/await,用此方法同步执行顺序
		async onLoad() {
			//async/await,当执行完APP生命周期中的onlaunch，再执行页面中的业务逻辑
			await this.$onLaunched;
			//执行页面中的业务逻辑
			this.API = getApp().globalData.API[0];
			this.requestData();
		},
		methods: {
			requestData() {
				uni.vrequest({
					url: this.API.LaunchesLatest,
					success: (res) => {
						this.LaunchesLatestData = res.data
						console.log(this.LaunchesLatestData);
						this.detail = res.data;
					}
				});
			}
		}
	}
</script>

<style lang="scss" scoped>
	.index {
		display: flex;
		flex-direction:column;
		justify-content: center; /* 水平居中 */
		align-items: center;     /* 垂直居中 */
		bottom: 0;
		.patch {
			height: 500rpx;
		}

		.cell-group {
			width: 80%;
			height: 100rpx;
			background-color: #fff;
		}
	}
</style>
